.card-img-top {
    max-width: 210px;
    max-height: 220px;
    align-self: center;
}
.card {
    height: 100%;
    padding-top: 10px;
}
.card-text {
    overflow: hidden;
    display: -moz-box;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -moz-line-clamp: 4;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
}
.card-img {
    display: flex;
    height: 220px;
    justify-content: center;
}
.in_stock {
    padding: 5px 16px;
    border-radius: 4px;
    font-size: 14px;
    background-color: #f4faf6;
    color: #00a046;
    display: inline-flex;
    align-items: center;
}
.in_stock:before {
    content: url("data:image/png;base64,");
    height: 30px;
}

